.main_container {
    display: flex;
    width: 100vw;
    height: 100vh;
    background-color: pearl;
}
.sidebar_outer {
    background-color: #253532;
    color: white;
    min-width: 150px;
    width: 20vw;
    max-width: 200px;
}
.sidebar_inner {
    width: 100%;
    padding-left: 15px;
    padding-right: 40px;
    padding-top: 10px;
    display: flex;
    flex-direction: column;
    row-gap: 20px;
}
.nav_item {
    width: auto;
    font-size: 24px;
    padding-left: 20px;
}
.nav_item > a {
    color: white;
    text-decoration: none;
    transition: 0.4s
}
.nav_item > a:hover {
    color: lightseagreen;
}
.nav_logo {
    font-size: 32px;
}
.nav_item element {
    color: black;
}