.crm_landing {
    background-image: url('https://i.imgur.com/ZAMgQWH.jpg');
    background-size: cover ;
    width: 100vw;
    height: 100vh;
    color: white;
    display: flex;
    justify-content: center;

}

.crm_landing_inner {
    display: flex;
    flex-direction: column;
    height: 100%;
    width: 260px;
    padding-left: 10px;
    padding-right: 10px;
    justify-content: center;
    row-gap: 30px;
    background-color: #253532;
    align-items: center;
}
.crm_landing_form {
    width: 250px;
    display: flex;
    flex-direction: column;
    row-gap: 20px;
    padding: 10px;
}
.offscreen {
    display: none;
}
.input_email_header, .input_password_header {
    font-size: 18px;
}
.input_email, .input_password {
    font-size: 18px;
}
.crm_landing_header {
    font-size: 24px;
    font-weight: bold;
}

.crm_landing_center_horizontal {
    display: flex;
    justify-content: center;
    width: 100%;
}
.crm_landing_login_button {
    display: flex;
    padding: .45em 1.4em;
    font-size: 16px;
    background-color: white;
    border: none;
    font-family: system-ui, -apple-system, BlinkMacSystemFont, 'Segoe UI', Roboto, Oxygen, Ubuntu, Cantarell, 'Open Sans', 'Helvetica Neue', sans-serif;
}
.crm_landing_login_button:hover{
    background-color: #888;
}
.errmsg {
    background-color: #FF0000;
    color: white;
    padding: 5px;
    border-radius: 10px;
    box-shadow: 1px 10px 24px -9px rgba(0,0,0,0.87);
    -webkit-box-shadow: 1px 10px 24px -9px rgba(0,0,0,0.87);
    -moz-box-shadow: 1px 10px 24px -9px rgba(0,0,0,0.87);
}

.login_prompt {
    color: white;
    font-size: 14px;
    text-decoration: underline;
    width: 100%;
    text-align: center;
    display: flex;
    justify-content: center;
    transition: 0.4s all;
}
.login_prompt:hover {
    color: lightseagreen;
}
.crm_landing_register{
    background-color: white;
  color: black;
  padding: .5em .75em;
  text-align: center;
  text-decoration: none;
  display: inline-block;
  font-family: system-ui, -apple-system, BlinkMacSystemFont, 'Segoe UI', Roboto, Oxygen, Ubuntu, Cantarell, 'Open Sans', 'Helvetica Neue', sans-serif;
}
.crm_landing_register:hover{
    background-color: #888;
}
