.absolute-overlay{
    position: absolute;
    left: 0px;
    top: 0px;
    width: 100%;
    height: 100%;
    z-index: 10;
    background-color: rgba(0,0,0,0.75);
    display: flex;
    justify-content: center;
    align-items: center;
    animation: fadeIn 0.4s
}
@keyframes fadeIn {
    from {
        opacity: 0;
    }
    to {
        opacity: 1;
    }
}
.center-modal {
    background-color: white;
    width: 800px;
    height: 550px;
    border-radius: 5px;
}
@keyframes fadeOut {
    from {
        opacity: 1;
    }
    to {
        opacity: 0;
    }
}
.fadeOut {
    animation: fadeOut 0.4s;
}
.modal-header {
    padding: 10px;
    border-bottom: 1px solid lightgrey;
    border-radius: 50px 50px 0px 0px;
    display: flex;
    justify-content: space-between;
}
.modal-title {
    font-size: 16px;

}
.modal-title > b {
    color: teal;
}
.close-button-icon {
    background-color: none;
    transition: 0.3s;
    padding: 3px;
}
.close-button-icon > svg {
    color: black;
    height:20px;
    width: 20px;
    transition: 0.3s;
}
.close-button-icon:hover {
    background-color: palevioletred;
}
.close-button-icon:hover > svg {
    color: white;
    height: 20px;
    width: 20px;
}
.modal-body {
    padding-top: 5%;
    padding-right: 10%;
    padding-left: 10%;
    font-size: 18px;
}
.stage-name-input {
    font-size: 18px;
    width: 100%;
    border:1px solid lightgrey;
    padding: 8px;
    margin-bottom: 20px;
}
.stage-config-header {
    margin-bottom: 10px;
}
.field-select-1 {
    margin-bottom: 20px;
}