.landing_page_main {
    display: flex;
    flex-direction: column;
    width: 100%;
    height: 100%;
}
.navbar {
    position: fixed;
    width: 90%;
    padding-left: 5%;
    padding-right: 5%;
    padding-top: 10px;
    padding-bottom: 10px;
    height: 30px;
    display: flex;
    justify-content: space-between;
    background-color: darkslategray;
    color: white;
    align-items: center;
}
.nav-left {
    display: flex;
    column-gap: 10px;
    font-size: 16px;
}
.nav-right {
    display: flex;
    column-gap: 10px;
    font-size: 16px;
}
.nav-right > a {
    color: white;
    background-color: teal;
    padding-left: 10px;
    padding-right: 10px;
    border-radius: 2px;
    border: 1px solid black;
    padding-top: 5px;
    padding-bottom: 5px;
    text-decoration: none;
}
.main-landing-content {
    margin-top: 70px;
    padding: 40px;
}