.main_content_container_search {
    padding-top: 10px;
    display: flex;
    flex-direction: column;
    max-height: 100%;
    width: 100%;
    position: relative;
}

.top_level_search {
    display: flex; 
    flex-direction: row;
    column-gap: 20px;
    height: 30px;
    width: calc(100%-10px);
    justify-content: start;
    background-color: beige;
    padding-left: 10px;
}
.search_filtering {
    display: flex;
    flex-direction: column;
    height: 100px;
    justify-content: center;
    align-items: start;
    padding-left: 10px;
}
.filter_row_show {
    display: flex;
    padding-left: 20px;
    font-size: 10px;
    color:  blue;
}
.row_1_filtering {
    display: flex;
    flex-direction: row;
    column-gap: 10px;
}
.row_2_filtering {
    display: flex;
    flex-direction: row;
    column-gap: 10px;
}
.sorts_and_saved {
    display: flex;
}
.search_results_container {

}
.sort_by_container {
    display: flex;
    flex-direction: row;
    height: 38px;
    align-items: center;

    padding-left: 10px;
    column-gap: 10px;
}
.sort_by_label {
    margin-right: 10px;
    font-weight: medium;
    font-size: 16px;
}
.sort_by_outer {
    width: 200px;
}
.select-saved-container {
    display: flex;
    flex-direction: row;
    height: 38px;
    align-items: center;

    padding-left: 10px;
    column-gap: 10px;
}
.saved-search-label {
    margin-right: 10px;
}
.saved-search-input {
    width: 200px;
}
.search-results-container {
    width: 100%;
    background-color: white;
    max-height: calc(100%-166px);
    padding-top: 20px;
    overflow-y:auto;
    display: flex;
}
.search-results {
    width: 500px;
    background-color: white;
    height: 100%;
    border-right: 1px solid lightgray;
    overflow-y: scroll;
    display: flex;
    flex-direction: column;
    align-items: center;
}
.contract-emphasis {
    width: 100%;
    height: auto;
    padding-bottom: 10px;
    margin-left: auto;
    margin-right: auto;
    overflow-y: auto;
    justify-content: center;
}
.contract-container {
    display: flex;
    flex-direction: column;
    border: 1px solid lightgrey;
    border-radius: 10px;
    padding-top: 10px;
    padding-bottom: 15px;
    font-size: 14px;
    padding-left: 4%;
    padding-right: 4%;
    width: 88%;
    row-gap: 2px;
    transition: 0.4s all;
}
.contract-title {
    font-weight: bold;
    font-size: 16px;
}
.contract-opportunity-type {
    display: flex;
}
.contract-opportunity-type-label {
    color: white;
    display: flex;
    padding: 5px;
    padding-left: 8px;
    padding-right: 8px;
    border-radius: 30px;
    width: auto;
    font-size: 12px;
}

.contract-two-column {
    display: flex;
    flex-direction: row;
    justify-content: space-between;
}
.contract-col-1 {
    display: flex;
    flex-direction: column;
    row-gap: 10px;
}
.contract-col-2 {
    display: flex;
    flex-direction: column;
    row-gap: 10px;
    margin-right: 20px;
}
.contract-container:hover {
    border-color: blue;
}

.search-button-class {
    font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen', 'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue', sans-serif;
    padding: 5px;
    padding-left: 9px;
    padding-right: 9px;
    border-radius: 6px;
    border: 1px solid ;
    transition: 0.4s all;
    background-color: seagreen;
    color: white;

}
.search-button-class:hover {
    background-color: black;
}

.focus-contract-container {
    display: flex;
    flex-direction: column;
    width: 90%;
    min-height: 100%;
    background-color: white;
    padding: 20px;
    border-radius: 20px;
    border: 2px solid  lightgrey;
}
.focus-contract.title-group {
    display: flex;
    justify-content: space-between;
}
.focus-contract-title {
    font-size: 18px;
    font-weight: 600;
}
.focus-contract-pipeline {
    display: flex;
}
.add-to-pipeline {
    display: flex;
    padding: 10px;
    padding-top: 6px;
    padding-bottom: 6px;
    border-radius: 5px;
    border: 1px solid seagreen;
    color: white;
    background-color: teal;
    font-size: 16px;
    column-gap: 5px;
    font-weight: 400;
}
.focus-contract-type {
    display: flex;
}
.focus-contract-opportunity {
    color: white;
    padding: 4px;
    padding-left: 8px;
    padding-right: 8px;
    border-radius: 5px;
    font-size: 14px;
}
.solicitation {
    background-color: goldenrod;
}
.synopsis {
    background-color: green;
}
.presolicitation {
    background-color: blueviolet;
}
.special {
    background-color: red;
}
.sources {
    background-color: orangered;
}
.award {
    background-color: blue;
}

.triple-column-focus {
    display: flex;
    justify-content: space-between;
    margin-right: 10px;
    margin-left: 10px;
}
.col-1-focus {
    display: flex;
    flex-direction: column;
    row-gap: 10px;
}
.col-2-focus {
    display: flex;
    flex-direction: column;
    row-gap: 10px;
}
.col-3-focus {
    display: flex;
    flex-direction: column;
    row-gap: 10px;
}
.contract-item-label-focus {
    font-size: 12px;
}
.contract-item-info-focus {
    font-size: 16px;
}
.description-header {
    font-size: 16px;
    font-weight: bold;
    margin-top: 10px;
}
.description-focus {
    display: flex;
    flex-direction: column;
}
.description-text {
    height: auto;
    
}